  <div class="modal fade" id="exampleModal_tex1" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Mitos sobre las ITS</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body text-start">

          <b>«Doble condón doble protección»</b>: Falso. La doble protección hace referencia es a protegerse de dos
          situaciones:
          un embarazo no planeado y las ITS. Es completamente falso que usar doble condón sirva para protegerse mejor de
          las ITS. Todo lo contrario, existe mayor riesgo de que se rompan o se deslicen cuando se usan al tiempo. Es
          suficiente con usar un solo condón de manera. <br>
          <b> «Si tiene una ITS, eso se notará»</b>: Falso. Este es uno de los mitos más peligrosos para la salud. Algunas
          ITS no
          se manifiestan (como el herpes genital) o, a veces, los síntomas pueden tardar un buen tiempo en aparecer. Por
          eso, la mejor opción es hacerse exámenes de laboratorio preventivos de manera periódica. El diagnóstico temprano
          es clave para controlar las ITS, especialmente en el caso del VIH. <br>

          <b> «Solo las personas que tienen varias parejas sexuales tienen el riesgo»</b>: Falso. Es cierto que cuantas
          más
          parejas sexuales se tengan, más expuesto se está a infectarse. Pero eso no quiere decir que las parejas estables
          estén libres de las ITS pues es posible que la infección llegue en un momento de nuestra vida, sin necesidad de
          desarrollar síntomas y que, más adelante, contagiemos a nuestra pareja actual. <br>

          <b> «La píldora anticonceptiva protege de las ITS»</b>: Falso. Las infecciones de transmisión sexual se contraen
          por
          contacto directo con la sangre infectada, por contacto con los fluidos corporales, por el uso compartido de
          equipos de inyección, de la madre al hijo, incluso, mediante el contacto piel con piel. Por tanto, la píldora
          anticonceptiva, un compuesto de hormonas que son eficaces para evitar un embarazo, no protege contra las
          infecciones de transmisión. <br>

          <b> «Es imposible tratar las infecciones de trasmisión sexual»</b>: Falso. Para todas las ITS existe
          tratamiento. Lo que
          sucede es que no todas son curables, como el VIH. Si llegas a tener algún cambio en el cuerpo o alguna relación
          sexual de riesgo, lo recomendable es consultar al servicio de salud y hablar de la situación sin temor.

        </div>
      </div>
